'use client';

import Link from 'next/link';
import PublicHeader from '@/components/PublicHeader';
import PublicFooter from '@/components/PublicFooter';
import { FiBriefcase, FiMapPin, FiClock, FiArrowRight } from 'react-icons/fi';

export default function CareerPage() {
  const jobOpenings = [
    {
      title: 'Senior Medical Doctor',
      department: 'Medical',
      location: 'Dhaka, Bangladesh',
      type: 'Full-time',
      experience: '5+ years',
      description: 'We are seeking an experienced medical doctor to join our team and provide expert healthcare services.'
    },
    {
      title: 'Frontend Developer',
      department: 'Technology',
      location: 'Remote / Dhaka',
      type: 'Full-time',
      experience: '3+ years',
      description: 'Join our tech team to build and improve our healthcare platform using modern web technologies.'
    },
    {
      title: 'Customer Support Specialist',
      department: 'Support',
      location: 'Dhaka, Bangladesh',
      type: 'Full-time',
      experience: '2+ years',
      description: 'Help our customers with inquiries and provide excellent support service.'
    },
    {
      title: 'Healthcare Data Analyst',
      department: 'Analytics',
      location: 'Dhaka, Bangladesh',
      type: 'Full-time',
      experience: '3+ years',
      description: 'Analyze healthcare data to improve our services and patient outcomes.'
    },
    {
      title: 'UI/UX Designer',
      department: 'Design',
      location: 'Remote / Dhaka',
      type: 'Full-time',
      experience: '2+ years',
      description: 'Design intuitive and beautiful user interfaces for our healthcare platform.'
    },
    {
      title: 'Backend Developer',
      department: 'Technology',
      location: 'Remote / Dhaka',
      type: 'Full-time',
      experience: '4+ years',
      description: 'Build robust backend systems to support our healthcare platform.'
    }
  ];

  const benefits = [
    'Competitive salary package',
    'Health insurance coverage',
    'Flexible working hours',
    'Remote work options',
    'Professional development opportunities',
    'Team building activities',
    'Paid time off',
    'Performance bonuses'
  ];

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="bg-teal-600 text-white py-20">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <Link href="/support" className="inline-flex items-center gap-2 text-teal-100 hover:text-white mb-6 transition-colors">
            <FiArrowLeft /> Back to Support
          </Link>
          <div>
            <h1 className="text-4xl sm:text-5xl lg:text-6xl font-bold mb-4">Career Opportunities</h1>
            <p className="text-xl text-teal-100 max-w-2xl">
              Join our mission to revolutionize healthcare. Explore exciting career opportunities with MediWise.
            </p>
          </div>
        </div>
      </header>

      {/* Benefits Section */}
      <section className="py-20 bg-gray-50">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-12">
            <h2 className="text-3xl sm:text-4xl font-bold text-gray-900 mb-4">Why Work With Us?</h2>
            <p className="text-gray-600 max-w-2xl mx-auto">
              We offer competitive benefits and a supportive work environment
            </p>
          </div>
          <div className="grid sm:grid-cols-2 lg:grid-cols-4 gap-6 max-w-4xl mx-auto">
            {benefits.map((benefit, index) => (
              <div key={index} className="bg-white p-6 rounded-2xl shadow-lg text-center">
                <div className="w-12 h-12 bg-teal-100 rounded-full flex items-center justify-center mx-auto mb-4">
                  <span className="text-2xl">âœ“</span>
                </div>
                <p className="text-gray-700 font-semibold">{benefit}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Job Openings */}
      <section className="py-20">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-12">
            <h2 className="text-3xl sm:text-4xl font-bold text-gray-900 mb-4">Open Positions</h2>
            <p className="text-gray-600">Find the perfect role for you</p>
          </div>
          <div className="grid sm:grid-cols-2 gap-6 max-w-6xl mx-auto">
            {jobOpenings.map((job, index) => (
              <div key={index} className="bg-white rounded-2xl shadow-lg hover:shadow-xl transition-shadow p-8 border border-gray-100">
                <div className="flex items-start justify-between mb-4">
                  <div>
                    <h3 className="text-2xl font-bold text-gray-900 mb-2">{job.title}</h3>
                    <span className="inline-block px-3 py-1 bg-teal-100 text-teal-600 rounded-full text-sm font-semibold mb-3">
                      {job.department}
                    </span>
                  </div>
                  <FiBriefcase className="text-4xl text-teal-600" />
                </div>
                <div className="space-y-2 mb-4 text-gray-600">
                  <div className="flex items-center gap-2">
                    <FiMapPin />
                    <span>{job.location}</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <FiClock />
                    <span>{job.type}</span>
                  </div>
                  <div className="text-sm">
                    <span className="font-semibold">Experience:</span> {job.experience}
                  </div>
                </div>
                <p className="text-gray-600 mb-6">{job.description}</p>
                <button className="w-full px-6 py-3 bg-teal-600 text-white rounded-lg hover:bg-teal-700 transition-colors font-semibold flex items-center justify-center gap-2">
                  Apply Now <FiArrowRight />
                </button>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA */}
      <section className="py-20 bg-gradient-to-r from-teal-600 to-teal-700">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h2 className="text-3xl sm:text-4xl font-bold text-white mb-4">Don't See a Role That Fits?</h2>
          <p className="text-xl text-teal-100 mb-8 max-w-2xl mx-auto">
            We're always looking for talented individuals. Send us your resume and we'll keep you in mind for future opportunities.
          </p>
          <Link
            href="/contact"
            className="inline-block px-8 py-4 bg-white text-teal-600 rounded-lg hover:bg-teal-50 transition-colors font-semibold text-lg"
          >
            Send Your Resume
          </Link>
        </div>
      </section>
    </div>
  );
}

